<template>
  <div class="funding">
    <img src="~@/assets/images/flower.png" class="flower" />
    <h2 class="h2">
      <!-- 둘째, 기부를 통해 뜻깊은 날로 만들기 -->
    </h2>
    <p>
      <!-- 인생에서 가장 행복한 날,<br />
      이 크나큰 행복을 나누면 어떨까 생각했습니다.<br />
      저희가 부자는 아니지만 마음만은 부자니까!<br />
      저희보다 더 필요로 하는 분들에게<br />
      작은 도움을 드리고 싶었습니다.<br />
      저희를 축하해주고 싶은 그 마음을<br />
      장기화된 코로나로 인해<br />
      끼니를 해결하지 못하는 아이들에게<br />
      따뜻한 한 끼로 나누어주세요.<br />
      감사한 마음 잘 모아서 전하고<br />
      나누어주신 만큼 더욱 행복하게 살겠습니다.<br /> -->
    </p>
    <div class="cover">
    <Button @click="groomFund">
      신랑측 계좌번호 
    </Button>
    <Button @click="brideFund">
      신부측 계좌번호
    </Button>
    <!-- 컴포넌트 MyModal -->
    <FundingModal v-if="modal">
      <!-- default 슬롯 콘텐츠 -->
      <div>
        <p>신랑 계좌<button class="button" @click="copyData1">복사</button></p>
        <p>{{this.bankMessage1}}</p>
        <p>{{this.bankNo1}}</p>
        <!-- <input type="text" v-model="message1"/> -->
      </div>
      <p></p>
      <div>
        <p>신랑 혼주 계좌<button class="button" @click="copyData2">복사</button></p>
        <p>{{this.bankMessage2}}</p>
        <p>{{this.bankNo2}}</p>
      </div>
      <!-- /default -->
      <!-- footer 슬롯 콘텐츠 -->
      <template slot="footer">
        <button class="closeButton" @click="closeModal">닫기</button>
      </template>
      <!-- /footer -->
    </FundingModal>
  </div>
  </div>
</template>

<script>
import FundingModal from './FundingModal.vue'

export default {
  name: "Divider",
  data() {
    return {
      modal: false,
      message: '',
      copyData: 'copy data'
    }
  },
  components: { FundingModal },
  methods: {
    groomFund() {
      // 신랑 및 신랑측 혼주 계좌 번호 모달창 띄우기
      this.bankMessage1 = '국민은행 (예금주: 이상민)';
      this.bankNo1 = '05904190723';
      this.bankMessage2 = '국민은행 (예금주: 이상우)';
      this.bankNo2 = '5151515151';
      this.modal= true
    },
    brideFund() {
      // 신부 및 신부측 혼주 계좌 번호 모달창 띄우기
      this.bankMessage1 = '국민은행 (예금주: 김민경)';
      this.bankNo1 = '123412341234';
      this.bankMessage2 = '국민은행 (예금주: 김민경)';
      this.bankNo2 = '123451234512';
      this.modal= true
    },
    copyData1(){
      // 임시의 textarea 생성
      const textarea = document.createElement("textarea");

      // body 요소에 존재해야 복사가 진행됨
      document.body.appendChild(textarea);
      
      // 복사할 특정 텍스트를 임시의 textarea에 넣어주고 모두 셀렉션 상태
      textarea.value = this.bankNo1;
      textarea.select();
      
      // 복사 후 textarea 지우기
      document.execCommand('copy');
      document.body.removeChild(textarea);
      this.$dialog.message.info('계좌번호 복사완료', {
        position: 'top-right',
        timeout: 1000
      })
    },
    copyData2(){
      // 임시의 textarea 생성
      const textarea = document.createElement("textarea");

      // body 요소에 존재해야 복사가 진행됨
      document.body.appendChild(textarea);
      
      // 복사할 특정 텍스트를 임시의 textarea에 넣어주고 모두 셀렉션 상태
      textarea.value = this.bankNo2;
      textarea.select();
      
      // 복사 후 textarea 지우기
      document.execCommand('copy');
      document.body.removeChild(textarea);      
      this.$dialog.message.info('계좌번호 복사완료', {
        position: 'top-right',
        timeout: 1000
      })
    },
    openModal() {
      this.modal = true
    },
    closeModal() {
      this.modal = false
    },  
  },
};
</script>

<style lang="scss" scoped>
.funding {
  padding-top: 48px;
  padding-bottom: 72px;
  padding-left: $padding-vertical;
  padding-right: $padding-vertical;
  text-align: center;

  .flower {
    width: 70px;
    margin-bottom: 26px;
  }
